import { Producto } from './producto.model';
import { DetalleOrden } from './detalle.orden.model';
import { TipoPago } from './tipo.pago.model';

export class Orden {

	public detalleOrdenList: Array<DetalleOrden> = [];
	public total: number = 0;


	constructor(productos: Array<Producto>){
		for (var p in productos){
			this.detalleOrdenList.push(new DetalleOrden(productos[p], this));
		}
	}
	
	reset(){
		for (var detalleOrden in this.detalleOrdenList){
			this.detalleOrdenList[detalleOrden].tipoPago = TipoPago.EFECTIVO;
			this.detalleOrdenList[detalleOrden].vendido = 0;
			this.detalleOrdenList[detalleOrden].feria = 0;
			this.detalleOrdenList[detalleOrden].calcularTotal();
		}
	}

	public calcularTotal(): void{
		this.total = 0;
		for (var detalleOrden in this.detalleOrdenList){
			this.total += this.detalleOrdenList[detalleOrden].total;
		}
	}
	
	public getRowForEfectivo() : Array<any> {
		return this.getRowFor(TipoPago.EFECTIVO);
	}

	public getRowForTarjeta() : Array<any> {
		return this.getRowFor(TipoPago.TARJETA);
	}

	public getRowForFeria() : Array<any> {
		return this.getRowFor(null);
	}

	private getRowFor(tipoPago : TipoPago) : Array<any> {
		var row = new Array();
		row.push(new Date().toLocaleDateString() + " " + new Date().toLocaleTimeString());
		if (tipoPago != null) { 
			row.push(tipoPago == TipoPago.EFECTIVO ? "Effectivo" : "Tarjeta");
		}

		for (var detalle in this.detalleOrdenList) {
			var detalleOrden = this.detalleOrdenList[detalle];
			row.push(tipoPago == null ? detalleOrden.feria : (detalleOrden.tipoPago == tipoPago ? detalleOrden.vendido : 0));
		}
		return row;
	}

}