import { Producto } from './producto.model';
import { TipoPago } from './tipo.pago.model';
import { Orden } from './orden.model';
export class DetalleOrden {

	public producto: Producto;
	public vendido: number = 0;
	public feria: number = 0;
	public tipoPago: string = TipoPago.EFECTIVO;
	public orden;
	total: number = 0;
	
	constructor(producto: Producto, orden: Orden){
		this.producto = producto;
		this.orden = orden;
	}

	public onChangeVendido(vendido: number): void {
		this.vendido = vendido;
		this.calcularTotal();
	}
	
	public toggleTipoPago(): void{
		switch (this.tipoPago){
			case TipoPago.EFECTIVO:
				this.tipoPago = TipoPago.TARJETA;
				break;
			case TipoPago.TARJETA:
				this.tipoPago = TipoPago.EFECTIVO;
				break;
		}
	}

	public calcularTotal(): void{
		this.total = this.producto.precio * this.vendido;
		this.orden.calcularTotal();
	}

	public minusVendido(): void{
		this.vendido = this.minus(this.vendido);
		this.calcularTotal();
	}

	public addVendido(): void{
		this.vendido = this.add(this.vendido);
		this.calcularTotal();
		console.log(this.orden);
	}

	public minusFeria(): void{
		this.feria = this.minus(this.feria);
	}

	public addFeria(): void{
		this.feria = this.add(this.feria);
	}

	public isEffectivo() : boolean {
		return this.tipoPago == TipoPago.EFECTIVO;
	}

	private minus(currentValue : number) : number {
		currentValue = currentValue - 1;
		return currentValue < 0 ? 0 : currentValue;
	}

	private add(currentValue : number) : number {
		currentValue = currentValue + 1;
		return currentValue;
	}
}