import { Component } from '@angular/core';

import { GapiSession } from '../infrastructure/sessions/gapi.session';
import { Producto } from '../models/producto.model';
import { Orden } from '../models/orden.model';
import { TipoPago } from '../models/tipo.pago.model';
import { RouterLinkWithHref } from '@angular/router';
import { NgZone } from '@angular/core';
//import { fromPromise } from 'rxjs/Observable/fromPromise';
import { Observable } from "rxjs";
import { from } from 'rxjs';
import { defer } from 'rxjs';
import { of } from 'rxjs';
import { Observer} from 'rxjs';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {

	private static valueInputOption = "USER_ENTERED"; // RAW => https://developers.google.com/sheets/api/guides/values
	private static valueRenderOption = "UNFORMATTED_VALUE"; // RAW => https://developers.google.com/sheets/api/reference/rest/v4/ValueRenderOption
	private static spreadsheetId = '1oajvWQJIqPETOX-FxzFNvTcwf1K2KeuNnGKJ-aHGDmo';
	private static rangeControlVentaAddRow = 'Control Venta!B5';
	private static rangeControlFeriaAddRow = 'Control Ferias!B5';
	private static rangeCatalogosProductos = 'Catalogos!B3:D22';
	private static controlVentaSheetId = 1798969314;
	private static controlFeriaSheetId = 1823964924; // This is the gid parameter from the url

	public productos: Array<Producto> = [];
	public orden: Orden;
	public tipoPagos = Object.values(TipoPago);
	public test = "test";

	constructor (
		private gapiSession: GapiSession,
		private ngZone: NgZone
		){
	}
	
	ngOnInit() {
		// use this to refresh the credentials
		//this.gapiSession.signOut();
		//this.gapiSession.signIn();
		this.load().subscribe( 
			productosRange =>
			{
				this.ngZone.run( () =>
					{
						var listaProductos = productosRange.result["values"];
						for(var index in listaProductos){
							var producto = listaProductos[index];
							this.productos.push(new Producto(producto[0], producto[1], producto[2]));
						}
						this.orden = new Orden(this.productos);
					}
				)
			}	
		);
	}

	public load(): Observable<any>{
		return from(
			gapi.client.request(
				{
					method: 'GET',
					// Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
					path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values/${AppComponent.rangeCatalogosProductos}?valueRenderOption=${AppComponent.valueRenderOption}`
				}
			));
	}

	registrarVenta(event: Event): void {
		event.preventDefault();
		const self = this;
		
		// Registers the Efectivo and the Tarjeta
		gapi.client.request(
			{
				method: 'POST',
				// Applies one or more updates to the spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets/batchUpdate 
				path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}:batchUpdate`, 
				body: {
					requests:[
						{
							// adds a 2 row in `Control Venta` starting at row 4 (one for efectivo, one for tarjeta)
							insertDimension:{
								range: {
									sheetId: AppComponent.controlVentaSheetId,
									dimension: "ROWS",
									startIndex: 4,
									endIndex: 6
								},
								inheritFromBefore: false
							}
						}
					]
				}			
			}
		).then(function(response)
			{
				gapi.client.request(
					{
						method: 'POST',
						// Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
						path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values:batchUpdate`, 
						body: {
							valueInputOption: AppComponent.valueInputOption,
							data: [
								{
									range: AppComponent.rangeControlVentaAddRow,
									values: [
										self.orden.getRowForEfectivo(),
										self.orden.getRowForTarjeta()
									]
								}
							]
						}			
					}
				).then(function(response)
					{
						console.log(response);
					}
				);
			}
		);
		
		// Registers the Feria
		gapi.client.request(
			{
				method: 'POST',
				// Applies one or more updates to the spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets/batchUpdate 
				path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}:batchUpdate`, 
				body: {
					requests:[
						{
							// adds a 1 row in `Control Ferias` starting at row 4
							insertDimension:{
								range: {
									sheetId: AppComponent.controlFeriaSheetId,
									dimension: "ROWS",
									startIndex: 4,
									endIndex: 5
								},
								inheritFromBefore: false
							}
						}
					]
				}			
			}
		).then(function(response)
			{
				gapi.client.request(
					{
						method: 'POST',
						// Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
						path: `https://sheets.googleapis.com/v4/spreadsheets/${AppComponent.spreadsheetId}/values:batchUpdate`, 
						body: {
							valueInputOption: AppComponent.valueInputOption,
							data: [
								{
									range: AppComponent.rangeControlFeriaAddRow,
									values: [
										self.orden.getRowForFeria()
									]
								}
							]
						}			
					}
				).then(function(response)
					{
						self.ngZone.run( () =>
						{
							console.log("resetting");
							self.orden.reset();
						});
					}
				);
			}
		);

		// GET sheet/tab ID
		/*gapi.client.request(
			{
				method: 'GET',
				path: `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}`
			}
		).then(function(response)
			{
				console.log(response);
			}
		);*/

		/*gapi.client.request(
			{
				method: 'GET',
				path: `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}/values/${spreadsheetRange}`
			}
		).then(function(response)
			{
				console.log(response);
			}
		);*/

	}

}
